import LayoutSign from "../layout/LayoutSign";
import RegisterStep1 from "../module/register/RegisterStep1";
import RegisterStep2 from "../module/register/RegisterStep2";
import { useStep } from "../context/stepContext";
import { useNavigate } from "react-router-dom";
import ArrowLeft from "../icon/ArrowLeft";
import ArrowRight from "../icon/ArrowRight";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useEffect, useState } from "react";
import axios from "axios";

const RegisterStep = [
  { id: 1, title: "Your Profile", step: <RegisterStep1 /> },
  { id: 2, title: "Consent To Trest", step: <RegisterStep2 /> },
];
const schame = yup.object({
  first: yup.string().required("Vui lng nhp first name"),
  last: yup.string().required("Vui lng nhp last name"),
  socialsecurity: yup.string().required("Vui lng nhp ocial security number"),
  email: yup.string().required("Vui lng nhp email ca bn "),
  // .min(10, "khng c nhp di 10 k t")
  // .max(32, "Khng nhp qu 32 k t")
  // .matches(/^[a-z][a-z0-9_.]{5,32}@[a-z0-9]{2,}(\.[a-z0-9]{2,4}){1,2}$/, {
  //   message: "khng ng nh dng",
  // })
  password: yup.string().required("vui lng nhp password ca bn"),
  // .min(6, "nhp ti thiu 6 k t")
  // .max(20, "Nhp ti a 20 k t")
  // .matches(
  //   /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
  //   {
  //     message:
  //       "Vui long Ti thiu t nht mt k t vit hoa, mt k t vit thng, mt s v mt k t c bit",
  //   }
  // ),
});
const Register = () => {


  // const [data, setData] = useState([])

  // useEffect(() => { 
  //   const fetchData = async () => {
  //     const result = await axios(
  //       'https://fakestoreapi.com/users'
  //     );
  //     setData(result.data);
  //   };
  //   fetchData();
  // }, []);
  // console.log(data);

  const {
    handleSubmit,
    control,
    formState: { errors },
  } = useForm({
    mode: "onChange",
    defaultValues: {
      first: "",
      last: "",
      password: "",
      socialsecurity: "",
      email: "",
      bdate: ""
    },
    resolver: yupResolver(schame),
  });
  // console.log("errors: ", errors);
  const { step, setStep } = useStep();

  const handleRegister = async (values) => {
    if (step === 0) {
      setStep(step + 1);
    }
    if (step === 1) {
      //handle register
      // console.log(values.first);

      const currentDate = new Date();
      const birthdate = new Date(Date.parse(values.bdate));
      if (currentDate <= birthdate) {
        alert("Birthdate must be lower or equal than today");
        return;
      }

      const partient = ({
        PAINTED_ID: values.socialsecurity,
        Name: values.first + " " + values.last,
        Email: values.email,
        PASSWORD: values.password,
        BIRTHDATE: values.bdate
      })
      console.log(partient);
      const response = await axios.post(`http://localhost:8080/patient/register`, {
        'id': values.socialsecurity,
        'name': values.first + " " + values.last,
        'email': values.email,
        'password': values.password,
        'birthdate': values.bdate.replace(/-/g, '/')
      });
      console.log(response);

      if (response.data === "register success") {
        alert("Enter OTP")
      }

    }
  };
  return (
    <LayoutSign
      header="Create New Account"
      childrenStyle="max-w-[1200px]"
    // nextStep={<NextStep />}
    >
      <form autoComplete="off" onSubmit={handleSubmit(handleRegister)}>
        <div className="overflow-hidden bg-white rounded-3xl">
          <div className="flex justify-between">
            {RegisterStep.length > 0 &&
              RegisterStep.map((item, index) => {
                return (
                  <div
                    className={`flex flex-1 first:rounded-[0px_48px_48px_0px] last:rounded-[48px_0px_0px_48px]  items-center  justify-center gap-4 rounded-full p-[21px] ${index === step
                      ? "bg-bgRegister text-textColor"
                      : "text-white opacity-20"
                      }`}
                    key={item.id}
                  >
                    <span
                      className={`text-[20px]  w-[42px] rounded-full flex items-center justify-center h-[42px] ${index === step ? "bg-white" : "bg-textColor text-white"
                        }`}
                    >
                      {item.id}
                    </span>
                    <span
                      className={`text-2xl ${index === step ? null : "text-textColor"
                        }`}
                    >
                      {item.title}
                    </span>
                  </div>
                );
              })}
          </div>
          <div>
            {step === 0 && <RegisterStep1 control={control} />}
            {step === 1 && <RegisterStep2 />}
          </div>
        </div>
        <NextStep></NextStep>
      </form>
    </LayoutSign>
  );
};

function NextStep() {
  const navigate = useNavigate();
  const { step, setStep } = useStep();
  let contentBack = "";
  switch (step) {
    case 0:
      contentBack = "Back To Login";
      break;
    case 1:
      contentBack = "Your Profile";
      break;
    default:
      break;
  }
  return (
    <div className="flex max-w-[1200px] justify-between mt-[38px] w-full">
      <div
        onClick={() => {
          if (step === 0) {
            // Handle content account
            navigate("/login");
          } else {
            setStep(step - 1);
          }
        }}
        className="flex items-center text-white cursor-pointer"
      >
        <div className="flex items-center justify-center w-[42px] h-[42px]">
          <ArrowLeft />
        </div>
        <span className="text-2xl font-semibold">{contentBack}</span>
      </div>
      <button
        type="submit"
        className="flex items-center cursor-pointer text-2xl bg-white p-[5px_52px] rounded-3xl text-[#6F3AFA]"
      >
        <span className="text-2xl font-semibold">
          {step === 1 ? "Register" : "Next"}
        </span>
        <div className="flex items-center justify-center w-[42px] h-[42px]">
          <ArrowRight />
        </div>
      </button>
    </div>
  );
}
export default Register;
